.pagination_list {
    display: flex;
    justify-content: center;
    margin-top: 70 / $setFs + rem;
}

.pagination_link {
    width: 45 / $setFs + rem;
    height: 45 / $setFs + rem;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    font-size: 16 / $setFs + rem;
    opacity: 0.25;
    @include acc;

    &.active,
    &:hover {
        opacity: 1;
    }

    &.prev,
    &.next {
        font-size: 20 / $setFs + rem;
    }
}